@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
img {
  height: auto;
  object-fit: contain;
  max-width: 100%;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.page {
  height: 100%;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}
.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0 auto;
  padding: 0;
}
.page__main {
  flex-grow: 1;
}
.container {
  width: 280px;
}
@media (min-width: 768px) {
  .container {
    width: 708px;
  }
}
@media (min-width: 1340px) {
  .container {
    width: 1220px;
  }
}
.button {
  display: inline-block;
  padding: 10px 15px;
  margin: 0;
  font-family: "Oswald", "Arial", sans-serif;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  background-color: #68b738;
  border: none;
  cursor: pointer;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}
.button--more {
  color: #444444;
  background-color: #f2f2f2;
}
@media (max-width: 767px) {
  .page-header__nav {
    border-bottom: 1px solid #e6e6e6;
    margin-bottom: -1px;
  }
}
@media (min-width: 768px) {
  .page-header__nav {
    display: grid;
    grid-template-columns: max-content 1fr;
    gap: 84px;
    width: 768px;
    margin: 0 auto;
    padding: 24px 30px 24px;
  }
}
@media (min-width: 1340px) {
  .page-header__nav {
    gap: 428px;
    width: 1340px;
    padding: 53px 60px 39px;
  }
}
@media (max-width: 767px) {
  .page-header__bar {
    display: grid;
    grid-template-columns: 34px 1fr 34px;
    margin: 0 auto;
    width: 320px;
    padding: 12px 20px 16px;
    gap: 20px;
  }
}
.page-header__logo {
  display: grid;
  align-items: center;
  grid-template-columns: 34px 1fr;
  gap: 16px;
}
@media (max-width: 767px) {
  .page-header__logo {
    grid-column: 1 / 3;
  }
}
.page-header__logo-img {
  display: block;
}
.page-header__logo-text {
  justify-self: center;
  max-width: 100%;
}
.page-header__toggler {
  align-self: center;
  width: 34px;
  height: 34px;
  background-color: transparent;
  background-image: url("../../img/icons/burger-icon.svg");
  background-repeat: no-repeat;
  background-position: center center;
}
.site-list {
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: #000000;
  text-transform: uppercase;
  text-align: center;
  list-style-type: none;
}
@media (min-width: 768px) {
  .site-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    column-gap: 15px;
    row-gap: 10px;
    font-weight: 400;
    font-size: 18px;
    line-height: 20px;
  }
}
@media (min-width: 1340px) {
  .site-list {
    font-size: 20px;
  }
}
.site-list__item {
  position: relative;
}
@media (max-width: 767px) {
  .site-list__item {
    padding: 12px 20px 14px;
    margin-top: -1px;
    border-top: 1px solid #e6e6e6;
  }
}
@media (min-width: 768px) {
  .site-list__item--current::after {
    position: absolute;
    content: "";
    left: 10px;
    right: 10px;
    bottom: 0;
    height: 2px;
    background-color: #68b738;
  }
}
.site-list__item--current .site-list__link {
  cursor: default;
}
.site-list__link {
  display: inline-block;
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .site-list__link {
    padding-left: 10px;
    padding-right: 10px;
  }
  .site-list__link:hover[href] {
    color: rgba(0, 0, 0, 0.6);
  }
  .site-list__link:active[href] {
    color: rgba(0, 0, 0, 0.3);
  }
}
@media (min-width: 1340px) {
  .site-list--contrast {
    color: #ffffff;
  }
}
@media (min-width: 1340px) {
  .site-list--contrast .site-list__item--current::after {
    background-color: #ffffff;
  }
}
@media (min-width: 1340px) {
  .site-list--contrast .site-list__link:hover[href] {
    color: rgba(255, 255, 255, 0.6);
  }
  .site-list--contrast .site-list__link:active[href] {
    color: rgba(255, 255, 255, 0.3);
  }
}
