}
.program-card__title {
  position: relative;
  margin-top: 0;
  margin-left: 71px;
  margin-bottom: 29px;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .program-card__title {
    position: static;
    margin-left: 0;
    margin-bottom: 24px;
    font-size: 36px;
