}
.program-card__title::before {
  position: absolute;
  content: "";
  top: 50%;
  left: -71px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #68b738;
  transform: translate(0, -50%);
}
@media (min-width: 768px) {
  .program-card__title::before {
